import React, { Component } from 'react'
import Tile from 'fundamental-react/lib/Tile/Tile.js';
import TileContent from 'fundamental-react/lib/Tile/Tile.js';

class OutputItem extends Component {

  render() {
      return (        
        <Tile className="outputLine" style={{background: this.props.SAValue}}>
          <TileContent title="Tile Title" className="outputLineCnt">
            <p>
              {this.props.textInput} - 
              <em>
                {this.props.score}
              </em>
            </p>
          </TileContent>
        </Tile>

        )
    }
  }
  
export default OutputItem