import React, { Component } from 'react';
/*import { SentimentAnalyzer } from 'node-nlp/lib/sentiment';*/
import OutputItem from './output.js';
import { SearchInput } from 'fundamental-react/lib/SearchInput';
import { Button } from 'fundamental-react/lib/Button';

import { SentimentAnalyzer } from 'node-nlp';

class Feed extends Component {

    constructor(props) {
        super(props);
        this.state = {outputListState: [], textInput: ""};
        this.onSubmitBtnClick = this.onSubmitBtnClick.bind(this);
    }

    nlpAnalysis(txt) {
        
        /*const manager = new NlpManager({ languages: ['en'] });*/        
        const sentiment = new SentimentAnalyzer({ language: ['en'] });
        let output = sentiment.getSentiment(txt)

        let colrOutput = this.colrConv(output)
        
        return [colrOutput, output.score]
    }

    colrConv(ipt) {

        let scr = ipt.score
        let colrDiff = 127.5 * scr
        let hexcodeConv = `rgb(${127.5 + colrDiff}, ${127.5 - colrDiff}, ${127.5 - Math.abs(colrDiff)})`

        return hexcodeConv

    }

    onSubmitBtnClick(event) {

        let inputText = this.state.textInput;

        let nlpOutput = this.nlpAnalysis(inputText);

        let outputItemComp = <OutputItem key={this.state.outputListState.length} textInput={inputText} SAValue={nlpOutput[0]} score={nlpOutput[1]} />;

        this.setState((state) => {

            let output = state.outputListState.concat(outputItemComp)

            return {outputListState: output}
        });

        console.log(this.state.outputListState.length)

    }

    render() {
      return (
          <div>
            <InputBar />
          </div>
        )
    }
}

class InputBar extends Feed {

    updateInputValue(evt) {
        this.setState({
            textInput: evt.target.value
    })};

    clearInput() {
        this.setState((state) => {
            return {textInput: ""}
        })
    };

    render() {
      return (
        <div>
        <div className="outputContainer">
            {this.state.outputListState}        
        </div>
        <div className="inputBox">
            <SearchInput
            noSearchBtn
            id="inputBox"
            onEnter={() => {                
                this.onSubmitBtnClick()
                this.clearInput()
                return
            }}
            className="Box"
            value={this.state.textInput}
            onChange={evt => this.updateInputValue(evt)}
            />
            <Button className="Btn" type="standard" onClick={this.onSubmitBtnClick}>
              Analyze
            </Button>
        </div>
        </div>
        )
    }
}
  
export default Feed